import {NgModule, ModuleWithProviders} from '@angular/core';
import {CommonModule} from '@angular/common';
import {OverlayModule} from '@angular/cdk/overlay';

import {AdaptFocusHolderModule} from '../focus-holder/index';
import {AdaptCloseModule} from '../close/index';

import {
  AdaptDropdownAnchorDirective,
  AdaptDropdownDirective,
  AdaptDropdownMenuDirective,
  AdaptDropdownMenuTemplateComponent,
  AdaptDropdownMenuTemplateDirective,
  AdaptDropdownToggleDirective,
  AdaptDropdownWindowComponent
} from './dropdown.component';
import {AdaptDropdownWindow2Component} from './dropdown-window2.component';

const ADAPT_DROPDOWN_DIRECTIVES = [
  AdaptDropdownAnchorDirective,
  AdaptDropdownDirective,
  AdaptDropdownMenuDirective,
  AdaptDropdownMenuTemplateDirective,
  AdaptDropdownMenuTemplateComponent,
  AdaptDropdownToggleDirective,
  AdaptDropdownWindow2Component,
  AdaptDropdownWindowComponent
];

@NgModule({
  imports: [CommonModule, OverlayModule, AdaptFocusHolderModule, AdaptCloseModule],
  declarations: ADAPT_DROPDOWN_DIRECTIVES,
  exports: ADAPT_DROPDOWN_DIRECTIVES,
  entryComponents: [AdaptDropdownWindowComponent, AdaptDropdownWindow2Component]
})
export class AdaptDropdownModule {
  /**
   * Importing with '.forRoot()' is no longer necessary, you can simply import the module.
   * Will be removed in a short while
   *
   * @deprecated 11.16.0
   */
  static forRoot(): ModuleWithProviders<AdaptDropdownModule> {
    return {
      ngModule: AdaptDropdownModule
    };
  }
}

export * from './dropdown.component';
export * from './dropdown-window2.component';
export * from './dropdown.model';
